import Router from 'vue-router'
import Login from '@/components/Login'

import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import AnnotationSentenceEvaluate from '@/components/AnnotationSentenceEvaluate'
import AnnotationSentenceOperate from '@/components/AnnotationSentenceOperate'
import AnnoToUserPage from '@/components/AnnoToUserPage'
import UpdateUserAccountPassword from '@/components/UpdateUserAccountPassword'
import UserAccount from '@/components/UserAccount'
import AnnotationTypeMaintain from '@/components/AnnotationTypeMaintain'

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'',
      component:Container,
      // leaf:true,
      roles:[''],
      children:[
        {path:'/uiAnnoToUserPage',roles:[''],component:AnnoToUserPage,name:''},
        {path:'/uiUserAccount',roles:[''],component:UserAccount,name:''}
        // {path:'/uiUpdateUserPassword',roles:['',''],component:UpdateUserAccountPassword,
        //   name:''}
      ]
    },
    {
      path:'/',
      name:'',
      component:Container,
      roles:['','',''],
      children:[
        {path:'/uiAnnotationSentenceOperate',roles:['',''],component:AnnotationSentenceOperate,name:''},
        // {path:'/uiAnnoPage',roles:['','',''],component:AnnoPage,name:''},
        {path:'/uiAnnotationSentenceEvaluate',roles:['',''],component:AnnotationSentenceEvaluate,name:''}
      ]
    },
    // {
    //   path:'/',
    //   name:'',
    //   component:Container,
    //   roles:[''],
    //   children:[
    //     {path:'/uiAnnotationTypeMaintain',roles:[''],component:AnnotationTypeMaintain,name:''}
    //   ]
    // },
    {
      path:'/uiLogin',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/uiLogin'}
    }
  ]
})



